body {
  margin: 0;
}

.container {
  justify-content: center;
  align-items: center;
  width: 100vmin;
  height: 100vmin;
  margin: 0 auto;
  display: flex;
}

.stopwatch {
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__dot {
  z-index: 2;
  background: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}

.stopwatch__minute-hand {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s steps(60, end) infinite rotate;
  position: absolute;
  top: 20vmin;
}

.stopwatch__minute-hand--speed-up {
  animation-duration: 600s;
}

.stopwatch__second-hand {
  z-index: 1;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate;
  position: absolute;
  top: 2vmin;
}

.stopwatch__second-hand--speed-up {
  animation-duration: 10s;
}

@keyframes rotate {
  from {
    transform-origin: bottom;
    transform: rotate(0);
  }

  to {
    transform-origin: bottom;
    transform: rotate(360deg);
  }
}
